    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p La selección, diseño e implementación de materiales didácticos y recursos pedagógicos para la interpretación turística en entornos étnicos debe regirse por principios de pertinencia cultural, sostenibilidad ambiental y eficacia pedagógica. Esta selección debe realizarse bajo un protocolo de validación con los sabedores y autoridades tradicionales, garantizando el respeto a los derechos de propiedad intelectual colectiva y evitando la reproducción no autorizada de símbolos, diseños o conocimientos sagrados, tal como lo establece el Decreto 1080 de 2015 sobre Derecho de Autor y la Política de Turismo Sostenible (MINCIT, 2022) en su componente de protección del patrimonio cultural. Para el proceso de selección de materiales se debe tener en cuenta que:

